@font-face {
  font-family: "Bebas Neue";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Bebas Neue"), url("../fonts/bebas-neue-regular.woff2") format("woff2"), url("../fonts/bebas-neue-regular.woff") format("woff"); }

@font-face {
  font-family: "Bebas Neue";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Bebas Neue"), url("../fonts/bebas-neue-bold.woff2") format("woff2"), url("../fonts/bebas-neue-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1200px;
  min-width: 1200px;
  margin: 0 auto;
  padding: 0 20px; }
  @media (max-width: 1199px) {
    .container {
      max-width: 1199px;
      min-width: 768px;
      padding: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      min-width: 320px;
      padding: 0 15px; } }

.button {
  font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  display: inline-block;
  min-height: 45px;
  padding: 20px 56px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.05em;
  vertical-align: middle;
  color: #ffffff;
  background: none;
  border: 4px solid #ffffff;
  cursor: pointer; }
  .button:hover, .button:focus {
    outline: 1px auto #000; }
  .button:active {
    opacity: 0.6; }

@media (max-width: 1199px) {
  .button {
    padding: 20px 30px; } }

@media (max-width: 767px) {
  .button {
    padding: 20px 82px; } }

.hero {
  width: 100%;
  min-height: 658px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); }

.hero__top {
  display: flex;
  justify-content: space-between;
  margin-top: 30px; }

.hero__tel {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.03em; }

.hero__main {
  display: flex;
  justify-content: space-between;
  margin-top: -4px; }

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 400px; }

.hero__content-city {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 50px;
  font-weight: 400;
  margin: 0;
  margin-top: 116px;
  color: #ffffff;
  letter-spacing: 0.03em;
  text-transform: uppercase; }

.hero__content-wrap {
  position: relative;
  margin-bottom: 56px; }

.hero__content-title {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 109px;
  line-height: 109px;
  font-weight: 700;
  width: 300px;
  margin: 0;
  margin-top: 10px;
  color: #ffffff;
  text-align: right;
  text-transform: uppercase;
  letter-spacing: 0.03em; }

.hero__content-text {
  font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  position: absolute;
  bottom: 27px;
  left: -80px;
  width: 115px;
  margin: 0;
  color: #ffffff;
  text-align: right; }

.hero__socials {
  display: flex;
  flex-direction: column-reverse;
  margin-left: 124px;
  margin-right: -5px; }

.hero__socials-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-bottom: 50px; }

.hero__socials-item:not(:last-of-type) {
  margin-bottom: 16px; }

@media (max-width: 1199px) {
  .hero {
    min-height: 554px;
    background: conic-gradient(from 139.4deg at 20.7% 35.74%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); }
  .hero__top {
    margin-top: 40px;
    margin-bottom: 18px; }
  .hero__logo-icon {
    width: 82px;
    height: 17px; }
  .hero__tel {
    font-family: "Bebas Neue", "Arial", sans-serif;
