    margin-top: 54px; }
  .hero__content-wrap {
    position: relative;
    margin-bottom: 40px; }
  .hero__content-title {
    font-family: "Bebas Neue", "Arial", sans-serif;
    font-size: 86px;
    line-height: 86px;
    font-weight: 700;
    width: 235px;
    margin-top: 5px; }
  .hero__content-text {
    font-family: "Roboto Condensed", "Arial", "Helvetica", sans-serif;
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
