@media (max-width: 767px) {
  .hero {
    min-height: 491px;
    background: conic-gradient(from 171.43deg at 35.31% 34.22%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg); }
  .hero__top {
